/**
 * 1 / 3   2
 * 0.33
 * @param numerator 
 * @param denominator 
 * @param precision 
 * @returns
 */
const toFloatPrecision = (numerator, denominator, precision ) => {

  let returnVal = Math.floor((numerator * 10 ** precision) / denominator) / 10 ** precision;

  if(isNaN(returnVal)){
    return 0;
  }

  return returnVal;
  
};

export { toFloatPrecision };
