import React , { useRef, useState } from "react";
import { httpPost } from "../util/apiClient";
import { httpUrl } from "../util/urlMapper";
import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";


function Login(){
    const [authMode, setAuthMode] = useState("signin");
    const inputRef = useRef([]);
    const navigate = useNavigate();

    const changeAuthMode = () => {
      setAuthMode(authMode === "signin" ? "signup" : "signin")
    }

    const loginFn = () => {
      console.log(inputRef);
      if(inputRef.current[0].value === ""){
        Swal.fire(' .');
        inputRef.current[0].focus();
        return false;
      }else if(inputRef.current[1].value === ""){
        Swal.fire(' .');
        inputRef.current[1].focus();
        return false;
      }

      httpPost(httpUrl.Login, {
        "email" :  inputRef.current[0].value,
        "password" : inputRef.current[1].value,
      })
      .then((res) => {
        console.log(res);

        navigate("/");
      })
      .catch((e) => { 
        Swal.fire('   .');
      });
    }

  
    if (authMode === "signin") {
      return (
        <div className="Auth-form-container">
          <form className="Auth-form"  action='' method='' onSubmit={loginFn}>
            <div className="Auth-form-content">
              <h3 className="Auth-form-title">Scouter</h3>
              <div className="text-center">
          
              </div>
              <div className="form-group mt-3">
                <label></label>
                <input
                  type="email"
                  className="form-control mt-1"
                  placeholder=""
                  maxLength={100}
                  ref={el => inputRef.current[0] = el}
                />
              </div>
              <div className="form-group mt-3">
                <label></label>
                <input
                  type="password"
                  className="form-control mt-1"
                  placeholder=""
                  maxLength={255}
                  ref={el => inputRef.current[1] = el}
                />
              </div>
              <div className="d-grid gap-2 mt-3">
                <button type="button" className="btn btn-primary" onClick={loginFn}>
                  
                </button>
              </div>

              <p className="text-center mt-2" onClick={()=>changeAuthMode()}>
                <a href="#!"></a>
              </p>
              <p className="text-center mt-2">
                <a href="#!"></a>
              </p>
            </div>
          </form>
        </div>
      )
    }
  
    return (
      <div className="Auth-form-container">
        <form className="Auth-form">
          <div className="Auth-form-content">
            <h3 className="Auth-form-title"></h3>
            <div className="text-center">
                ?{" "}
              <span className="link-primary" onClick={changeAuthMode}>
                
              </span>
            </div>
            <div className="form-group mt-3">
              <label></label>
              <input
                type="email"
                className="form-control mt-1"
                placeholder=""
                maxLength={100}
              />
            </div>
            <div className="form-group mt-3">
              <label></label>
              <input
                type="text"
                className="form-control mt-1"
                placeholder=""
                maxLength={20}
              />
            </div>
            <div className="form-group mt-3">
              <label></label>
              <input
                type="password"
                className="form-control mt-1"
                placeholder=""
                maxLength={255}
              />
            </div>
            <div className="d-grid gap-2 mt-3">
              <button type="submit" className="btn btn-primary">
                
              </button>
            </div>
          </div>
        </form>
      </div>
    )

}

export default Login;